import {
  Streamlit,
  withStreamlitConnection,
  ComponentProps,
} from "streamlit-component-lib"
import React, { useEffect, useState, useMemo, useRef } from "react"
import { ChevronDown, ChevronRight, Brain, CheckCircle2, CircleDashed } from "lucide-react"
import { motion, AnimatePresence } from "framer-motion"
import ReactMarkdown from "react-markdown"
import remarkMath from "remark-math"
import rehypeKatex from "rehype-katex"
import "katex/dist/katex.min.css"

const ReasoningVisualizer = (props: ComponentProps) => {
  const { args } = props
  const rawText = args["text"] || ""
  // Default to collapsed since this component is now primarily shown *after* reasoning is done.
  const [isExpanded, setIsExpanded] = useState(false)
  const containerRef = useRef<HTMLDivElement>(null)

  // Parse the text into thought and answer
  const { thought, answer, isThinking } = useMemo(() => {
    // Define supported tag pairs. Order matters (prioritize more specific or common ones)
    const TAG_PAIRS = [
      { start: /<think>/i, end: /<\/\s*think\s*>/i },
      { start: /\[THOUGHT\]/i, end: /\[\/THOUGHT\]/i },
      { start: /<reasoning>/i, end: /<\/\s*reasoning\s*>/i },
      { start: /<chain_of_thought>/i, end: /<\/\s*chain_of_thought\s*>/i }
    ]

    let thoughtContent = ""
    let answerContent = ""
    let currentlyThinking = false
    let matchedPair = null

    // 1. Try to find a matching start tag
    for (const pair of TAG_PAIRS) {
      const match = rawText.match(pair.start)
      if (match) {
        // Found a start tag!
        const startIndex = match.index! + match[0].length
        const remainingText = rawText.slice(startIndex)

        // Check for end tag
        const endMatch = remainingText.match(pair.end)

        if (endMatch) {
          // Full thought block found
          thoughtContent = remainingText.slice(0, endMatch.index).trim()
          answerContent = remainingText.slice(endMatch.index! + endMatch[0].length).trim()
        } else {
          // Formatting is incomplete, still thinking
          thoughtContent = remainingText.trim()
          currentlyThinking = true
          // Answer is whatever follows, or empty if we consider it all thought
          // Usually if tag is open, everything is thought until closed
          answerContent = ""
        }
        break; // Stop after first valid start tag found
      }
    }

    // 2. Fallback if no tags found
    if (!thoughtContent && !currentlyThinking) {
      answerContent = rawText
    }

    // Helper to wrap \boxed{} in $$ for block math rendering if not already wrapped
    const processText = (text: string) => {
      if (!text) return ""
      return text.replace(/(\\boxed\{(?:[^{}]|\{(?:[^{}]|\{[^{}]*\})*\})*\})/g, (match, p1, offset, fullStr) => {
        const before = fullStr.substring(Math.max(0, offset - 3), offset)
        const after = fullStr.substring(offset + match.length, offset + match.length + 3)
        if (before.includes("$$") && after.includes("$$")) {
          return match
        }
        return `$$\n${match}\n$$`
      })
    }

    return {
      thought: processText(thoughtContent),
      answer: processText(answerContent),
      isThinking: currentlyThinking
    }
  }, [rawText])

  // Adjust height on expand/collapse or content change
  useEffect(() => {
    const updateHeight = () => {
      if (containerRef.current) {
        // Add some padding to bottom to avoid scrollbar flickering
        Streamlit.setFrameHeight(containerRef.current.scrollHeight + 10)
      } else {
        Streamlit.setFrameHeight()
      }
    }

    // Immediate update
    updateHeight()

    // Update after animation
    const timeoutId = setTimeout(updateHeight, 250)

    // Also use ResizeObserver for robust height tracking
    const resizeObserver = new ResizeObserver(() => {
      updateHeight()
    })

    if (containerRef.current) {
      resizeObserver.observe(containerRef.current)
    }

    return () => {
      clearTimeout(timeoutId)
      resizeObserver.disconnect()
    }
  }, [isExpanded, thought, answer])

  return (
    <div
      ref={containerRef}
      style={{
        fontFamily: "'Inter', 'Segoe UI', Roboto, sans-serif",
        color: "#1f2937",
        display: "flex",
        flexDirection: "column",
        gap: "1rem",
        // Ensure no overflow cropping by Streamlit iframe default
        overflow: "visible"
      }}>
      {/* THOUGHT PROCESS SECTION */}
      {thought && (
        <div style={{
          border: "1px solid #e5e7eb",
          borderRadius: "0.75rem",
          overflow: "hidden",
          backgroundColor: "#f9fafb",
          boxShadow: "0 1px 2px 0 rgba(0, 0, 0, 0.05)"
        }}>
          <div
            onClick={() => setIsExpanded(!isExpanded)}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              padding: "0.75rem 1rem",
              cursor: "pointer",
              backgroundColor: "#f3f4f6",
              borderBottom: isExpanded ? "1px solid #e5e7eb" : "none",
              transition: "background-color 0.2s"
            }}
            onMouseEnter={(e) => e.currentTarget.style.backgroundColor = "#e5e7eb"}
            onMouseLeave={(e) => e.currentTarget.style.backgroundColor = "#f3f4f6"}
          >
            <div style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
              <div style={{
                padding: "4px",
                backgroundColor: isThinking ? "#dbeafe" : "#d1fae5",
                borderRadius: "6px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center"
              }}>
                <Brain size={16} color={isThinking ? "#2563eb" : "#059669"} />
              </div>
              <span style={{
                fontWeight: 600,
                fontSize: "0.9rem",
                color: "#374151"
              }}>
                Reasoning Process
              </span>
              {isThinking && (
                <span style={{
                  fontSize: "0.75rem",
                  padding: "2px 8px",
                  borderRadius: "9999px",
                  backgroundColor: "#eff6ff",
                  color: "#3b82f6",
                  border: "1px solid #bfdbfe",
                  marginLeft: "0.5rem",
                  display: "flex",
                  alignItems: "center",
                  gap: "4px"
                }}>
                  <motion.div
                    animate={{ rotate: 360 }}
                    transition={{ repeat: Infinity, duration: 1, ease: "linear" }}
                  >
                    <CircleDashed size={12} />
                  </motion.div>
                  Thinking...
                </span>
              )}
            </div>
            {isExpanded ? <ChevronDown size={18} color="#6b7280" /> : <ChevronRight size={18} color="#6b7280" />}
          </div>

          <AnimatePresence>
            {isExpanded && (
              <motion.div
                initial={{ height: 0, opacity: 0 }}
                animate={{ height: "auto", opacity: 1 }}
                exit={{ height: 0, opacity: 0 }}
                transition={{ duration: 0.2 }}
                style={{ overflow: "hidden" }}
              >
                <div style={{
                  padding: "1rem",
                  fontSize: "0.9rem",
                  lineHeight: "1.6",
                  color: "#4b5563",
                  backgroundColor: "#ffffff",
                  fontFamily: "'JetBrains Mono', 'Fira Code', monospace",
                  whiteSpace: "pre-wrap"
                }}>
                  <ReactMarkdown
                    remarkPlugins={[remarkMath]}
                    rehypePlugins={[rehypeKatex]}
                  >
                    {thought}
                  </ReactMarkdown>

                  {isThinking && (
                    <motion.span
                      animate={{ opacity: [0, 1, 0] }}
                      transition={{ repeat: Infinity, duration: 0.8 }}
                      style={{ display: "inline-block", marginLeft: "4px", fontWeight: "bold", color: "#3b82f6" }}
                    >
                      
                    </motion.span>
                  )}
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </div>
      )}

      {/* FINAL ANSWER SECTION */}
      {answer && (
        <div style={{
          padding: "0.5rem 0",
          animation: "fadeIn 0.5s ease-in"
        }}>
          {!thought && (
            <div style={{
              marginBottom: "0.5rem",
              display: "flex",
              alignItems: "center",
              gap: "0.5rem",
              color: "#059669",
              fontWeight: 600
            }}>
              <CheckCircle2 size={18} />
              <span>Response</span>
            </div>
          )}
          <div style={{
            fontSize: "1rem",
            lineHeight: "1.6",
            color: "#111827",
            // Remove whiteSpace pre-wrap to let Markdown handle spacing
          }}>
            <ReactMarkdown
              remarkPlugins={[remarkMath]}
              rehypePlugins={[rehypeKatex]}
            >
              {answer}
            </ReactMarkdown>
          </div>
        </div>
      )}

      {/* Global styles for animations if needed */}
      <style>{`
        @keyframes fadeIn {
          from { opacity: 0; transform: translateY(5px); }
          to { opacity: 1; transform: translateY(0); }
        }
        /* Markdown Styles */
        p { margin-bottom: 1rem; }
        strong { font-weight: 600; color: #111; }
        code { background: #f3f4f6; padding: 2px 4px; border-radius: 4px; font-size: 0.9em; }
        pre { background: #1e293b; color: #fff; padding: 1rem; border-radius: 8px; overflow-x: auto; }
        pre code { background: transparent; color: inherit; padding: 0; }
        .katex { font-size: 1.1em; }
      `}</style>
    </div>
  )
}

export default withStreamlitConnection(ReasoningVisualizer)
